  whiteBoxElements: string[] = ['html', 'body', '#app', '#root']; // // ç½å±æ£æµçå®¹å¨åè¡¨
  silentWhiteScreen = false; // æ¯å¦å¼å¯ç½å±æ£æµ
  skeletonProject = false; // é¡¹ç®æ¯å¦æéª¨æ¶å±
  filterXhrUrlRegExp: any; // è¿æ»¤çæ¥å£è¯·æ±æ­£å
  handleHttpStatus: any; // å¤çæ¥å£è¿åç response
